import React from "react"
import {Link, NavLink, useNavigate} from "react-router-dom"
import './css/Navigation.css'


const NavigationBar = (props) => {
    let navigate = useNavigate()
    const logOutRedirect = () => {
        props.logOut()
        navigate('/')
    }
    return (
        <div className="navbar navbar-inverse">
            <div className="container">
                <div className="navbar">
                    <Link className="navbar-brand" to='/'>TO-DO</Link>
                </div>
                <div className="link-box">
                    <ul className="nav-link-custom">
                        <li className="list-inline-item">
                            {props.isAuthenticated() ?
                                <button type='button' className="btn btn-secondary btn-lg"
                                        onClick={logOutRedirect}>
                                      {props.username}</button> :
                                <NavLink to='/login'>  </NavLink>}
                        </li>
                        <li className="list-inline-item"><NavLink to='/users'></NavLink></li>
                        <li className="list-inline-item"><NavLink to='/projects'></NavLink></li>
                        <li className="list-inline-item"><NavLink to='/todos'></NavLink></li>
                    </ul>
                </div>
            </div>
        </div>
    )
}


export default NavigationBar
